.popup {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: rgba(0, 0, 0, .5);
	z-index: 1;
	height: 0;
	overflow: hidden;
	opacity: 0;
	transition: opacity .25s ease-in, height 0ms ease-in .25s;
}
